<template>
  <div class="form">
  	<form @submit.prevent="addFilter">
    <h2>Filters</h2>
    <div class="field-block">
      <h4>Filter word or phrase</h4>
  	   <input type="text" name="textFilter" v-model="textFilter" title="Text Filter" placeholder="Word to filter"/>
    </div>
    <div class="field-block">
      <h4>Add regular expression filter</h4>
  	   <input type="text" name="regexFilter" v-model="regexFilter" title="regex Filter" placeholder="Regular expression filter"/>
    </div>
    <div class="form-buttons">
      <input value="Add filter" type="submit">
    </div>
    <div class="field-block" v-if="textFilters.length > 0">
      <h4>
        My filters
      </h4>
      <p style="color:#911;font-size:small" v-if="textFilter.length < 3">
        Use 3 or more filters for better performance
      </p>
      <div class="topic-name" v-for="(item, index) in textFilters">
        {{ item }} <a href="javascript:void(0);" @click="removeTextFilter(item)">[x]</a>
      </div>
    </div>
    </form>

  	<form @submit.prevent="setValues">
    <h2>Bitdb settings</h2>
    <div class="field-block">
      <h4>Bitdb node</h4>
  	   <input type="text" name="bitdbNode" v-model="bitdbNode" title="Bitdb node" placeholder="Bitdb node address"/>
    </div>
    <div class="field-block">
      <h4>Bitdb API key</h4>
      <input type="text" name="bitdbApiKey" v-model="bitdbApiKey" title="Bitdb API key" placeholder="Bitdb API key"/>
    </div>
    <div class="field-block">
      <h4>Bitdb address format</h4>
      <input type="radio" name="bitdbAddressFormat" value="legacy" v-model="bitdbAddressFormat" title="Bitdb Address Format" placeholder="Bitdb Address Format"/> Bitcoin Address (1G....)
      <input type="radio" name="bitdbAddressFormat" value="cashaddr" v-model="bitdbAddressFormat" title="Bitdb Address Format" placeholder="Bitdb Address Format"/> cashaddr (qp...)
    </div>
    <div class="field-block">
      <h4>Block explorer RPC</h4>
      <input type="text" name="blockExplorerRpc" v-model="blockExplorerRpc" title="Block explorer RPC" placeholder="Block explorer RPC"/>
    </div>
    <div class="field-block">
      <h4>Bitsocket node</h4>
  	   <input type="text" name="bitsocketNode" v-model="bitsocketNode" title="Bitsocket node" placeholder="Bitsocket node address"/>
    </div>
    <div class="form-buttons">
      <input value="submit" type="submit">
      <p style="color:#666;font-size:small">Saved settings are stored only in your browser.</p>
    </div>
  </form>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

  export default {
    name: 'Settings',

    data () {
      return {
        textFilter: null,
        regexFilter: null,
        bitdbNode: null,
        bitdbApiKey: null,
        bitdbAddressFormat: null,
        bitsocketNode: null,
        blockExplorerRpc: null
      }
    },
    components: {
    },
    mounted () {
      this.bitdbNode = window.bitdbNode
      this.bitdbApiKey = window.bitdbApiKey
      this.bitdbAddressFormat = window.bitdbAddressFormat
      this.blockExplorerRpc = window.blockExplorerRpc
      this.bitsocketNode = window.bitsocketNode
    },
    computed: {
      ...mapState([
        'textFilters'
      ])
    },
    methods: {
      ...mapActions([
        'addTextFilter',
        'removeTextFilter'
      ]),
      addFilter () {
        if (this.textFilter) {
          let filter = ''
          filter = '^((?!' + this.textFilter + ').)*$'
          this.addTextFilter(filter)
        }
        //  ^((?![0-9a-f]{8,217}).)*$
        if (this.regexFilter) {
          this.addTextFilter(this.regexFilter)
        }
        this.textFilter = ''
        this.regexFilter = ''
      },
      setValues () {
        if (this.bitdbNode && this.bitdbNode !== window.bitdbNodeDefault) {
          if (this.bitdbNode === window.bitdbBCHNodeDefault) {
            localStorage.setItem('networkName', 'BCH')
            window.networkName = 'BCH'
          } else {
            localStorage.setItem('networkName', 'Custom')
            window.networkName = 'Custom'
          }
          localStorage.setItem('bitdbNode', this.bitdbNode)
          window.bitdbNode = this.bitdbNode
        } else {
          localStorage.removeItem('networkName')
          localStorage.removeItem('bitdbNode')
          window.networkName = window.networkNameDefault
          window.bitdbNode = window.bitdbNodeDefault
        }

        if (this.bitdbApiKey && this.bitdbApiKey != window.bitdbApiKeyDefault) {
          localStorage.setItem('bitdbApiKey', this.bitdbApiKey)
          window.bitdbApiKey = this.bitdbApiKey
        } else {
          localStorage.removeItem('bitdbApiKey')
          window.bitdbApiKey = window.bitdbApiKeyDefault
        }

        if (this.bitdbAddressFormat && this.bitdbAddressFormat != window.bitdbAddressFormatDefault) {
          localStorage.setItem('bitdbAddressFormat', this.bitdbAddressFormat)
          window.bitdbAddressFormat = this.bitdbAddressFormat
        } else {
          localStorage.removeItem('bitdbAddressFormat')
          window.bitdbAddressFormat = window.bitdbAddressFormatDefault
        }

        if (this.blockExplorerRpc && this.blockExplorerRpc != window.blockExplorerRpcDefault) {
          localStorage.setItem('blockExplorerRpc', this.blockExplorerRpc)
          window.blockExplorerRpc = this.blockExplorerRpc
        } else {
          localStorage.removeItem('blockExplorerRpc')
          window.blockExplorerRpc = window.blockExplorerRpcDefault
        }

        if (this.bitsocketNode && this.bitsocketNode != window.bitsocketNodeDefault) {
          localStorage.setItem('bitsocketNode', this.bitsocketNode)
          window.bitsocketNode = this.bitsocketNode
        } else {
          localStorage.removeItem('bitsocketNode')
          window.bitsocketNode = window.bitsocketNodeDefault
        }

        location.reload()
      }
    }
  };
</script>

<style>
</style>



